import React from "react";
import { CCard, CCardBody, CForm, CButton, CFormInput, CFormLabel } from "@coreui/react";
import { toast, ToastContainer } from 'react-toastify';
import Constants, { userToken } from 'src/Constants';
const CopyWriteInfo = () => {

    const [content, setContent] = React.useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();
        // console.log(content);
        let url = Constants.staticContentUpdate + 'copyright';
        let body = JSON.stringify({ content })
        // console.log(body)
        let response = await fetch(url, {
            method: 'POST',
            headers: {
                'Authorization': userToken(),
                'content-Type': 'application/json'
            },
            body
        })

        let data = await response.json();
        // console.log(data);
        if (data.status === 200) {
            toast.success(data.message);
        } else {
            toast.error('Something Went Wrong!');
        }


    }

    React.useState(() => {
        let url = Constants.staticContent + 'copyright';
        (async () => {
          let response = await fetch(url);
          let data = await response.json();
          if(data.status === 200){
            setContent(data?.data?.content)
          }
        })()
      }, [])

    return (
        <>

            <ToastContainer
                position="top-right"
                theme="colored"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover />

            <CCard>
                <CCardBody>
                    <CForm onSubmit={handleSubmit}>
                        <div className="mb-3">
                            <CFormLabel htmlFor="name">Copyright Information<span className="text-danger">*</span></CFormLabel>
                            <CFormInput
                                type="text"
                                placeholder="copyright info"
                                defaultValue={content} onChange={(e) => setContent(e.target.value)}
                            />
                        </div>

                        <div className="mb-3">
                            <CButton type='submit' color="primary" className="px-4">
                                Update
                            </CButton>
                        </div>
                    </CForm>
                </CCardBody>
            </CCard>

        </>
    )
}
export default CopyWriteInfo;